@import "../../Shared/Style/GeneralStyle.scss";

.myProjects {
  margin: 80px 0px;
  background-image: url(../../Shared/Assets/Imgs/doodleItems.jpg);
  // margin-top: 100px;
  background-repeat: repeat;
  height: 100vh;
  // padding-left: 120px;
  padding: 100px 0px;
  height: fit-content;
  z-index: 2;
  position: relative;
}
.myProjects__container {
  padding-top: 48px;
}
.myProjects__head {
  padding-left: 120px;
}
.myProjects__title {
  text-align: left;
  @include BoldFont64;
  color: $BrightSecondColor;
  span {
    color: $BrightMainColor;
  }
  margin: 0 0 16px 0;
}
.myProject__filter {
  padding: 0px;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: left;
  gap: 10px;
}
.myProject__choise {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 10px 32px;
  @include FontNormal18;
  width: fit-content;
  background: $DarkSecondColor;
  border-radius: 24px;
  color: $BrightSecondColor;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
  &.myProject__choise-chicked {
    background-color: $BrightMainColor;
  }
}

.myProjects__boxes {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(368px, 1fr));
  gap: 30px;
  margin: auto;
}
.myProjects__box {
  margin: auto;
}

@media (max-width: $LaptopWidth) {
  .myProjects__title {
    @include BoldFont44;
  }
  .myProject__choise {
    @include FontNormal18;
  }
}
@media (max-width: $IpadWidth) {
  .myProjects__head{
    padding: 0px;
  }
  .myProjects__title{
    text-align: center;
  }
  .myProject__filter{
    justify-content: center;
    flex-direction: column;
  }

}

@media (min-width: 768px) {
  .myProjects__container{
    padding: 0px 70px;
  }
}